  { id: 'compare', label: 'Ï¢ÖÌï© ÎπÑÍµê', icon: BarChart3, description: 'ÌîÑÎ°úÏ†ùÌä∏Í∞Ñ ÏÑ±Í≥º ÎπÑÍµê' }
